@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  background-color: rgb(61, 61, 65);
}

.App{
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
}

.navbar {
    background-color: #36495d;
    height: 10%;
    display: flex;
    justify-content: start;
    align-items: center;
    /* position: sticky; */
  }
  
.nav-menu {
  width: 18%;
  height: 100vh;
  float: left;
  overflow-y: scroll;  
  display: flex;
  justify-content: center;
}

  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 150px;
    overflow-y: scroll;
  }

.centerNavbar {
  justify-content: center;
  align-items: center;
}

.test {
  color: #f5f5f5;
  font-size: 60px;
  text-decoration: none;
  justify-content: center;
  display: flex;
  align-items: center;
}
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 8px;
    border-radius: 10px;
  }
  
  .nav-text a:hover {
    background-color: #595a5c;
  }
  




.question {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  vertical-align: top;
}

.aboutpage {
  display: flex;
  justify-content: center;
  align-items: center;
}

.question-title {
  padding:5%;
}

.hmm {
  overflow-y: scroll;
  color: #f5f5f5;
  font-size: 25px;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  padding-top: 2%;
  padding-right: 5%;
  padding-left: 5%;
  text-align: justify;
}
